import ListItem from '../../../components/ListItem/ListItem.vue'

import UserModel from '../../../models/user.js';
import PostModel from '@/models/job.js'
const postModel = new PostModel()
const userModel = new UserModel() 
export default {
	data(){
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			jobList: [],
			pageNum: 1,
			pageSize: 10,
			label: '',
			loading: false, 
			hasEnd: false,
		}
	},
	onReady() {
		this.getPublishJobList()
	}, 
	components:{
		ListItem
	},
	methods:{
		showDetail(id){
			uni.navigateTo({
				url: '../../PostDetail/PostDetail?id='+id
			})
		},

		getUserInfo(e){
			console.log(e)
		},
		switchToCustomer(){
			const that = this
			uni.showLoading({
				mask: true
			})
			uni.login({
				provider: 'weixin',
				success(res){
					if(res.errMsg === 'login:ok'){
						// 执行后台登录
						userModel.wxLogin(res.code).then(res=>{
							uni.hideLoading()
							const { code, message, data } = res.data
							if(code === '0'){
								// 将返回的token存入本地
								uni.setStorageSync('token', data.token)
								that.hasToken = true
								uni.redirectTo({
									url: '../../customer/main/main'
								})
							}else{
								uni.showToast({
									icon: 'none',
									title: message
								})
							}
						}).catch(err=>{
							uni.hideLoading()
							uni.showToast({
								icon: 'none',
								title: '切换失败，请稍后再试'
							})
						})
					}else{
						uni.hideLoading()
						uni.showToast({
							icon: 'none',
							title: res.errMeg
						})
					}
				},
				fail(err) {
					console.log(err)
				}
			})
		},
		getPublishJobList(){
			const that = this
			postModel.publishJobList(this.pageNum, this.pageSize, this.label).then(res=>{
				uni.hideLoading()
				uni.stopPullDownRefresh()
				const { code, message, data } = res.data
				if(code === '0'){
					if(this.pageNum === 1){
						that.jobList = data.list
					}else {
						that.jobList = [...that.jobList, ...data.list]
					}
					if(that.jobList.length === data.total){
						that.hasEnd = true
					}
				}else {
					uni.showToast({
						icon: 'none',
						title: res.message
					})
				}
			}).catch(err=>{
				uni.hideLoading()
				uni.stopPullDownRefresh()
				uni.showToast({
					icon: 'none',
					title:'获取项目信息失败'
				})
			})
		},
		refresh(){
			this.pageNum = 1
			this.hasEnd = false
			this.label = ''
			this.getPublishJobList()
		},
		getNext(){
			if(!this.hasEnd){
				this.pageNum += 1
				this.getPublishJobList()
			}
		},
		switchLabel(label){
			this.label = label
			console.log(this.label)
			this.pageNum = 1
			this.hasEnd = false
			this.getPublishJobList()
		},
		addJob(){
			uni.navigateTo({
				url: '../../public/addJob/addJob'
			})
		}
	}
}
